import styles from './enterNumber.module.scss'
import {useState} from "react";
import {CodeInput} from "../enterCode/CodeInput.jsx";
import {apiRequests} from "../../shared/api/apiRequests.js";
import NewPassword from '../setNewPassword/setNewPassword.jsx';
import {SelectCountries} from "../selectCountries/selectCountries";
import {allTimeZone} from "../../shared/constants/countries";

const phoneReg = /^[+]*[(]{0,1}[0-9]{1,4}[)]{0,1}[-\s\./0-9]*$/


export const EnterNumber = () => {
    const [step, setStep] = useState('enterNumber')
    const [numberValue, setNumber] = useState('')
    const [country, setCountry] = useState(allTimeZone[0])

    const handleRequest = async () => {
        const number = `${country.phone_code.replace('+', '')}${numberValue}`
        await apiRequests.user.requestCode(Number(number))
            .then((res) => {
                if (res.data.status === 'success') {
                    setNumber(number)
                    setStep('code')
                }
            })
            .catch((e) => {
                if (e.response.data.detail === "      ") {
                   alert('  ')
                } else {
                    alert(' ')
                }
            })
    }

    const handleChange = (val) => {
        if (phoneReg.test(val) || val === '') {
            setNumber(val)
        }
    }


    return (
        <>
            <div className="mobile-container">
                <div className={styles.body}>
                    <button className={styles.previousStep}>
                        <img src={step} alt=""/>
                    </button>
                    <header className={styles.header}>
                        <h4> </h4>
                        <p>{ step === 'new-password' ? '  ' : step === 'code' ? '   ' : '  ,   '}</p>
                    </header>

                    {
                        step === 'enterNumber' ? (
                            <div className={styles.container}>
                                <div className={styles.form}>
                                    <SelectCountries country={country} setCountry={setCountry} />
                                    <div className={styles.inputBody}>
                                        <input 
                                            value={numberValue}
                                            onChange={(e) => handleChange(e.target.value)}
                                            type="text" 
                                            placeholder={' '}
                                        />
                                    </div>
                                </div>

                                <div onClick={handleRequest} className={styles.sendBtn}>
                                    <button disabled={numberValue === '' ? true : false}> </button>
                                </div>
                            </div>
                        ) : step === 'code' ? <CodeInput number={numberValue} changeStep={setStep} /> : <NewPassword  />
                    }

                </div>

            </div>
        </>
    )
}