import { useDispatch, useSelector } from 'react-redux';
import { apiRequests } from '../../shared/api/apiRequests';
import styles from './setNewPassword.module.scss'
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Cookies from 'js-cookie';
import {setUserData, setUserPicture} from '../../shared/store/main';
import passVisible from '../../shared/images/password.svg'
import passwordIco from '../../shared/images/showPassword.png'
import hidePasswordIco from '../../shared/images/hidePassword.png'
const NewPassword = () => {
    const [passwordValue, setPassword] = useState('')
    const dispatch = useDispatch()
    const {mainSlice} = useSelector(state => state)
    const navigate = useNavigate()
    const [isVisiblePass, setVisible] = useState(false)
    
    const handleSubmit = async () => {
        const body = {
            token: mainSlice.token,
            phone: mainSlice.number,
            password: passwordValue
        }
        await apiRequests.user.resetPassword(body)
            .then((res) => {
                if (res.data.status === 'success') {
                    apiRequests.user.authorization({phone: body.phone, password: body.password})
                        .then((res) => {
                            Cookies.set('accessToken', res.data.access_token)
                            Cookies.set('refreshToken', res.data.refresh_token)
                            apiRequests.user.getData()
                                .then((res) => {
                                    dispatch(setUserData(res.data))
                                    navigate('/profile')
                                    return res.data
                                })
                                .then((res) => {
                                    apiRequests.user.getPicture(res.avatar_id)
                                        .then((res) => {
                                            dispatch(setUserPicture(URL.createObjectURL(res.data)))
                                        })
                                        .catch((e) => {
                                            console.log('fetch picture: ', e)
                                        })
                                })
                        })
                }
            })
    }


  return (
    <div className={styles.wrapper}>
        <div className="main-input password">
            <label htmlFor=""> </label>
            <input type={isVisiblePass ? 'text' : 'password'} value={passwordValue} onChange={(e) => setPassword(e.target.value)} />
            <button onClick={() => setVisible(prev => !prev)}>
                <img src={isVisiblePass ? hidePasswordIco : passwordIco} alt=""/>
            </button>
        </div>
        <button onClick={handleSubmit} className="green-button"></button>
    </div>
  )
};

export default NewPassword;
