import { useEffect, useState } from 'react';
import styles from './editPassword.module.scss'
import {Link, useNavigate} from "react-router-dom";
import { apiRequests } from '../../shared/api/apiRequests';
import { useSelector } from 'react-redux';
import step from "../../shared/images/previousStep.svg"
import passwordIco from "../../shared/images/showPassword.png";
import hidePasswordIco from "../../shared/images/hidePassword.png";
export const EditPassword = () => {
    const navigate = useNavigate()
    const [isVisiblePass, setVisible] = useState(false)
    const {isAuth} = useSelector(state => state.mainSlice)
    const [password, setPassword] = useState({
        old_password: '',
        new_password: '',
        repeat_password: ''
    })

    const handleSubmit = async () => {
        if (password.repeat_password !== password.new_password) {
            return alert('  ')
        }
        await apiRequests.user.updatePassword({
            old_password: password.old_password,
            new_password: password.new_password
        })
            .then((res) => {
                alert('  ')
            })
            .catch((e) => {
                alert(' ')
            })
    }

    return (
        <div className={'mobile-container'}>
            <header className="mobile-header">
                <Link to={'/profile'}>
                    <img src={step} alt=""/>
                </Link>
                <h4> </h4>
            </header>

            <div className={styles.form}>
                <div className="main-input password">
                    <button onClick={() => setVisible(prev => !prev)}>
                        <img src={isVisiblePass ? hidePasswordIco : passwordIco} alt=""/>

                    </button>
                    <input value={password.old_password} onChange={(e) => setPassword(
                        prev => ({
                            ...prev,
                            old_password: e.target.value
                        })
                    )} type={isVisiblePass ? 'text' : 'password'} placeholder={' '}/>
                </div>
                <div className="main-input password">
                    <button onClick={() => setVisible(prev => !prev)}>
                        <img src={isVisiblePass ? hidePasswordIco : passwordIco} alt=""/>

                    </button>
                    <input value={password.new_password} onChange={(e) => setPassword(
                        prev => ({
                            ...prev,
                            new_password: e.target.value
                        })
                    )} type={isVisiblePass ? 'text' : 'password'} placeholder={' '}/>
                </div>
                <div className="main-input password">
                    <button onClick={() => setVisible(prev => !prev)}>
                        <img src={isVisiblePass ? hidePasswordIco : passwordIco} alt=""/>
                    </button>
                    <input value={password.repeat_password} onChange={(e) => setPassword(
                        prev => ({
                            ...prev,
                            repeat_password: e.target.value
                        })
                    )} type={isVisiblePass ? 'text' : 'password'} placeholder={' '} />
                </div>
            </div>

            <button onClick={handleSubmit} className={'green-button'}> </button>
        </div>
    )
}