import styles from './historyOfTrip.module.scss'
import backIco from '../../shared/images/previousStep.svg'
import {Link} from "react-router-dom";
import {useEffect, useState} from "react";
import {apiRequests} from "../../shared/api/apiRequests";

export const HistoryOfTrip = () => {
    const [trips, setTrips] = useState([])
    useEffect(() => {
        const getInitialData = async () => {
            await apiRequests.user.trips()
                .then((res) => {
                    setTrips(res.data)
                })
        }
        getInitialData()
    }, []);

    const translateName = (initialName) => {
        switch (initialName) {
            case 'roller_skates':
                return ' '
                break;
            case 'skateboard':
                return ''
                break;
            case 'bike':
                return ''
                break;
            case 'electric_scooters':
                return ''
                break;
            case 'unicycle':
                return ''
                break;
        }
    }

    return (
        <div className={'mobile-container'}>
            <header className={'mobile-header'}>
                <Link to={'/profile'}>
                    <img src={backIco} alt=""/>
                </Link>
                <h4> </h4>
            </header>

            <div className={styles.wrapper}>
                {
                    trips.map((item) => (
                        <div className={styles.card}>
                            <h4>{translateName(item.category)}</h4>
                            <div className={styles.cardWrapper}>
                                <span>. : {item.max_speed} /</span>
                                <span>: {item.distance.toFixed(2)} </span>
                                <span>: {item.duration > 60 ? (item.duration / 60).toFixed(2) : item.duration} </span>
                            </div>
                        </div>
                    ))
                }
            </div>
        </div>
    )
}