import styles from './authorization.module.scss'
import {SelectCountries} from "../../components/selectCountries/selectCountries";
import {useState} from "react";
import {apiRequests} from "../../shared/api/apiRequests";
import Cookies from "js-cookie";
import {setUserData, setUserPicture} from "../../shared/store/main";
import {useDispatch} from "react-redux";
import {Link, useNavigate} from "react-router-dom";
import passwordIco from '../../shared/images/showPassword.png'
import hidePasswordIco from '../../shared/images/hidePassword.png'
import {allTimeZone} from "../../shared/constants/countries";

const phoneReg = /^[+]*[(]{0,1}[0-9]{1,4}[)]{0,1}[-\s\./0-9]*$/

export const AuthorizationPage = () => {
    const [country, setCountry] = useState(allTimeZone[0])
    const [numberValue, setNumberValue] = useState('')
    const [password, setPassword] = useState('')
    const [isVisiblePass, setVisible] = useState(false)
    const dispatch = useDispatch()
    const navigate = useNavigate()

    const handleChange = (value) => {
        if (phoneReg.test(value) || value === '') {
            setNumberValue(value)
        }
    }

    const handleSubmit = async () => {
        const number = `${country.phone_code.replace('+', '')}${numberValue}`
        await apiRequests.user.authorization({
            phone: number,
            password
        })
            .then((res) => {
                Cookies.set('accessToken', res.data.access_token)
                Cookies.set('refreshToken', res.data.refresh_token)

                apiRequests.user.getData()
                    .then((res) => {
                        dispatch(setUserData(res.data))
                        navigate('/profile')
                        return res.data
                    })
                    .then((res) => {
                        apiRequests.user.getPicture(res.avatar_id)
                            .then((res) => {
                                dispatch(setUserPicture(URL.createObjectURL(res.data)))
                            })
                            .catch((e) => {
                                console.log('fetch picture: ', e)
                            })
                    })
            })
            .catch((e) => {
                if (e.response.data.detail === "Incorrect Email or Password") {
                    alert('   ')
                } else {
                    alert(' ')
                }
            })
    }

    return (
        <div>
            <div className="mobile-container">
                <header className="mobile-header">
                    <h4>  </h4>
                </header>

                <div className={styles.wrapper}>
                    <div className={styles.form}>
                        <SelectCountries country={country} setCountry={setCountry} />
                        <div className={styles.inputBody}>
                            <input
                                value={numberValue}
                                onChange={(e) => handleChange(e.target.value)}
                                type="text"
                                placeholder={' '}
                            />
                        </div>
                    </div>
                    <div className={styles.passBody}>
                        <input
                            value={password}
                            placeholder={' '}
                            onChange={(e) => setPassword(e.target.value)}
                            type={isVisiblePass ? 'text' : 'password'}
                            className={styles.inputPassword}
                        />
                        <button onClick={() => setVisible(prev => !prev)}>
                            <img src={isVisiblePass ? hidePasswordIco : passwordIco} alt=""/>
                        </button>
                    </div>

                    <Link className={styles.resetPassword} to={'/reset_password'}>
                         
                    </Link>
                </div>

                <button onClick={handleSubmit} className={'green-button'}></button>
            </div>
        </div>
    )
}